<template>
  <div>
    <b-card
      v-if="user.division === null"
      no-body
      class=""
    >
      <div
        class="saj-text d-flex align-items-center justify-content-center bg-white p-1 m-1"
        style="font-style: italic; background: rgb(247, 247, 252); text-align: center; height: 500px; border-radius:20px "
      >

        {{ $t('Only Head of Division can view') }}

      </div>
    </b-card>
    <b-card
      v-else
      no-body
      class=""
    >
      <div class="">
        <b-row class="pt-1 ml-1 saj-text">
          <p>
            {{ $t("Division's Name") }} : {{ division.name }}
          </p>
        </b-row>
        <b-row class="pt-1 ml-1 saj-text">
          <p>
            {{ $t("Division's Details") }} : {{ division.details }}
          </p>
        </b-row>
        <b-row class="p-1">
          <!-- v-if="roles.isAdmin" -->
          <div class="d-flex flex-column ml-1">
            <label
              class="saj-text"
              style="margin-left: 1px;"
            >{{ $t("Cycle") }}:</label>
            <!-- <div class="bg-danger"> -->
            <Datepicker
              id="input-id"
              v-model="selectedCycle"
              :value="customFormatter"
              :format="customFormatter"
              minimum-view="year"
              name="datepicker"
              input-class="year-custom"
              class="show-only-year"
            />
            <!-- </div> -->
          </div>
          <div class="d-flex flex-column ml-1">
            <label
              class="saj-text"
              style="margin-left: 1px;"
            >{{ $t("Department") }}:</label>
            <b-form-select
              v-model="selectDepartment"
              :options="deptList"
              :value="deptList"
              size="sm"
              class="saj-text"
              style="width:200px"
            />
          </div>
        </b-row>

        <b-row class="p-1 d-flex justify-content-end align-items-end">
          <!-- <b-button
            md="4"
            variant="primary"
            class="saj-button d-flex justify-content-center mt-3"
            block
            style=""
            @click="clearButton()"
          /> -->
          <!--
          3. <p> {{ roles }} </p> -->
          <!-- 4. <p> {{ graphValue }} </p> -->
          <!-- ini<p> {{ catType }} </p>
          -->
          <!-- <p>1. {{ selectedCompany }}</p>
          2. <p> {{ isOverall }} </p>
          <p>6. {{ roles.isAdmin }} </p> -->
          <!-- <p>5. {{ filterBy }}</p> -->
          <b-button
            md="4"
            variant="primary"
            class="saj-button mr-1"
            block
            style="width: 120px; height: 45px;"
            :disabled="filterBy === null && selectedCompany !== 0"
            @click="filter(), graphData = [], refreshGraph += 1, show = false, selectedCompany !== 0 ? isOverall = false : isOverall = true, search = true"
          >
            <!-- <feather-icon icon="SearchIcon" /> -->
            <span
              class=""
              style=""
            > {{ $t("Filter") }} </span>
          </b-button>
          <b-button
            md="4"
            variant="primary"
            class="mr-1 saj-button"
            block
            style="width: 120px; height: 45px;"
            :disabled="filterBy === null && selectedCompany !== 0"
            @click="clearButton()"
          >
            <span
              class=""
              style=""
            > {{ $t("Clear") }} </span>
          </b-button>
        </b-row>
      </div>
      <!-- {{ graphData }} -->
    </b-card>
    <b-overlay
      :show="search"
      rounded="sm"
      class="mt-3"
    >
      <template #overlay>
        <div
          class="d-flex flex-column align-items-center justify-content-center"
          style="margin-top: 30vh;"
        >
          <b-spinner
            variant="primary"
            label="Spinning"
          />
          <p class="mt-1">
            {{ $t('Fetching Data') }}...
          </p>
        </div>

      </template>
      <div v-if="show">
        <div
          v-if="isOverall && roles.isAdmin"
          :key="refreshGraph"
          class=""
        >
          <overall-graph
            :graph="graphValue"
            :category-type="catType"
          />
        </div>
        <div
          v-else
          :key="refreshGraph"
          class="m-0"
        >
          <b-tabs content-class="mt-3">
            <b-tab
              :title="$t('Bar Graph')"
              active
            >
              <bar-graph
                :graph="graphValue"
                :category-type="catType"
                :is-div="true"
              />
            </b-tab>
            <b-tab :title="$t('Line Graph')">
              <line-graph
                :graph="graphValue"
                :category-type="catType"
                :is-div="true"
              />
            </b-tab>
            <b-tab :title="$t('Overall Graph')">
              <overall-graph
                :graph="graphValue"
                :category-type="catType"
              />
            </b-tab>
          </b-tabs>

        </div>
      </div>

      <b-card v-if="show">
        <!-- summary table -->
        <span class="saj-title">{{ $t("Summary Table") }}</span>
        <b-tabs content-class="mt-1">
          <b-tab
            :title="$t('Performance Grade')"
            active
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList"
                :fields="summaryFields"
                :filter="searchSummary"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>

              </b-table>
            </div>
          </b-tab>
          <b-tab
            :title="$t('HOD Evaluation')"
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary1"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList1"
                :fields="summaryFields"
                :filter="searchSummary1"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>

              </b-table>
            </div>
          </b-tab>
          <b-tab
            :title="$t('Committee Evaluation')"
          >
            <div class="container">
              <div class="d-flex justify-content-end">
                <b-form-input
                  v-model="searchSummary2"
                  style="width: 250px;"
                  :placeholder="$t('Search') + '...'"
                  size="sm"
                  class="d-flex align-items-end"
                />
              </div>

              <b-table
                sticky-header="500px"
                responsive
                :items="summaryList2"
                :fields="summaryFields"
                :filter="searchSummary2"
                bordered
                show-empty
                style=""
                class="mt-1 mb-1 saj-subtitle"
              >
                <!-- <template #head()="data">
                <span
                  class="saj-text d-flex justify-content-center"
                >{{ $t(data.label) }}</span>
              </template> -->
                <template #emptyfiltered="">
                  <h4 style="text-align: center; font-style: italic">
                    {{ $t('There are no records to show') }}
                  </h4>
                </template>
                <template #thead-top="">
                  <b-tr class="saj-title text-center">
                    <!--core-->
                    <b-th
                      class=""
                      variant="primary"
                    >
                      <!-- asdad -->
                      {{ $t('Filtered Items') }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Need Improvement") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Satisfactory") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Very Good") }}
                    </b-th>
                    <b-th
                      class="text-center saj-subtitle"
                      style=""
                      variant="primary"
                      :colspan="2"
                    >
                      {{ $t("Excellent") }}
                    </b-th>
                  </b-tr>
                </template>

              </b-table>
            </div>
          </b-tab>
        </b-tabs>
      </b-card>
    </b-overlay>
  </div>
</template>

<script>
import {
  BCard,
  BTr,
  BTh,
  BRow,
  // BCol,
  BButton,
  BTable,
  BFormInput,
  // BCardGroup,
  // BPagination,
  BTab,
  BTabs,
  BOverlay,
  BSpinner,
  BFormSelect,
} from "bootstrap-vue"
import { mapGetters } from 'vuex'
import Datepicker from 'vuejs-datepicker'
import moment from "moment"
import OverallGraph from '@/views/pages/performance-statistics/overall_graph.vue'
import LineGraph from '@/views/pages/performance-statistics/line_graph.vue'
import BarGraph from '@/views/pages/performance-statistics/bar_graph.vue'
// import statisticsHod from '@/views/pages/performance-statistics/performance-statistics_hod.vue'

export default {
  components: {
    BCard,
    BRow,
    // BCol,
    BButton,
    BTable,
    Datepicker,
    BFormInput,
    BTr,
    BTh,
    // BCardGroup,
    // BPagination,
    BTab,
    BTabs,
    OverallGraph,
    LineGraph,
    BarGraph,
    BOverlay,
    BSpinner,
    BFormSelect,
    // statisticsHod,
  },

  data() {
    return {
      search: true,
      show: false,
      graphValue: null,
      catType: null,
      refreshGraph: 0,
      currentPage: 1,
      currFirstIdx: 0,
      currLastIdx: 6,
      rows: null,
      searchSummary: null,
      searchSummary1: null,
      searchSummary2: null,
      apex1Counter: 0,
      //   isSelectCompany: false,
      defaultDate: '2022',
      DatePickerFormat: 'yyyy',
      filterBy: null,
      tempSummaryField: [
        {
          label: '',
          key: 'filtered_items',
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: '%',
          key: 'perluperingatan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total4',
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: '%',
          key: 'memuaskan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total3',
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: '%',
          key: 'baik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total2',
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: '%',
          key: 'sangatbaik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total1',
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: '%',
          key: 'cemerlang',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total',
        },
      ],
      summaryFields: [], // column name === score
      summaryList: [], // filtered item
      summaryList1: [],
      summaryList2: [],
      tempListFilter: [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: 'Department',
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: 'Category',
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: 'Worklocation',
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: 'Gender',
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: 'Gradeset',
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: 'Grade',
        },
      ],
      selectedCycle: new Date(),
      cycleList: [],
      selectedCategory: null,
      categoryList: [{
        value: 1,
        text: 'Executive',
      }, {
        value: 2,
        text: 'Non-Executive',
      }],
      listFilterBy: [],
      selectedCompany: 0,
      initCompanyList: [{
        value: 0,
        text: 'All',
      }],
      colors: ['#3C4EF2', '#00ACC1', '#FFA346', '#9646EF', "#999fff", "#88bcfc", "#00838F", "#C4C4C4", "#B2EBF2"],
      companyList: [],
      graphData: [],
      isOverall: false,
      user: null,
      division: null,
      deptList: [{ value: null, text: ' Choose Department' }],
      selectDepartment: null,
      // options: [{ value: null, text: 'Department' }],
    }
  },
  computed: {
    ...mapGetters(['roles', 'userDetails']),
  },
  watch: {
    // eslint-disable-next-line func-names
    '$i18n.locale': function () {
      // this.getCompanyList()
      this.checkRoleFilter()
      this.tempSummaryField = [
        {
          label: '',
          key: 'filtered_items',
        },
        {
          // label: `${this.$i18n.t(`Need Improvement`)}`,
          label: '%',
          key: 'perluperingatan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total4',
        },
        {
          // label: `${this.$i18n.t(`Satisfactory`)}`,
          label: '%',
          key: 'memuaskan',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total3',
        },
        {
          // label: `${this.$i18n.t(`Good`)}`,
          label: '%',
          key: 'baik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total2',
        },
        {
          // label: `${this.$i18n.t(`Very Good`)}`,
          label: '%',
          key: 'sangatbaik',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total1',
        },
        {
          // label: `${this.$i18n.t(`Excellent`)}`,
          label: '%',
          key: 'cemerlang',
        },
        {
          label: `${this.$i18n.t(`Total Employee`)}`,
          key: 'total',
        },
      ]
      this.tempListFilter = [
        {
          text: `${this.$i18n.t(`Choose Category`)}`,
          value: null,
          disabled: true,
        },
        {
          text: `${this.$i18n.t(`Department`)}`,
          value: 'Department',
        },
        {
          text: `${this.$i18n.t(`Category`)}`,
          value: 'Category',
        },
        {
          text: `${this.$i18n.t(`Work Location`)}`,
          value: 'Worklocation',
        },
        {
          text: `${this.$i18n.t(`Gender`)}`,
          value: 'Gender',
        },
        {
          text: `${this.$i18n.t(`Grade Set`)}`,
          value: 'Gradeset',
        },
        {
          text: `${this.$i18n.t(`Grade`)}`,
          value: 'Grade',
        },
      ]
      this.listFilterBy = this.tempListFilter
      this.filter()
    },
  },
  mounted() {
    this.getCompanyList()
    // this.checkRoleFilter()
    this.listFilterBy = this.tempListFilter
    this.currentUser()

    // this.filter()
  },
  methods: {
    currentUser(){
      this.$axios.get(`${this.$baseUrl}/users/current_user`).then(res => {
        // console.log('Current User', response)
        this.user = res.data.data
        // console.log('res', res.data.data)

        if (this.user.division !== null){
          this.getDivision()
          this.filter()
        } else {
          this.search = false
        }
      })
    },
    getDivision(){
      // console.log(this.user)
      this.$axios.get(`${this.$baseUrl}/divisions/get_all_divisions?division_id=${this.user.division}`).then(res => {
        // this.user = res.data.data
        // console.log('res', res.data.data)
        this.division = res.data.data
        this.getAllDepartment = this.division.departments
        const a = this.getAllDepartment.map(dept => ({
          value: dept.id,
          text: dept.name,
        }))

        this.deptList = this.deptList.concat(a)

        // console.log(this.deptList)
      })
    },

    range(start, end) {
      // const arr = [1, 2, 3, 4, 5].map((_, idx) => start + idx)
      // console.log('arr', arr)
      // console.log(end, start)
      // console.log('array 5', Array(end - start))
      return Array(end - start !== 0 ? (end - start) : 1).fill().map((_, idx) => start + idx)
    },
    clearButton() {
      this.selectDepartment = null
      this.selectedCompany = 0
      this.filterBy = null
      this.selectedCycle = new Date()
      // console.log(this.filterBy)
      // this.filter()
      this.isOverall = true
      this.show = false
      this.search = true
      this.refreshGraph += 1

      this.filter()
      this.graphData = []
    },
    customFormatter(date) {
      this.scoreYearSelected = moment(date).format('YYYY')
      return this.scoreYearSelected
    },
    getCompanyList() { // get all company
      const config = {
        method: 'get',
        url: `${this.$baseUrl}/subsidiary/getAll`,
      }

      this.$axios(config)
        .then(response => {
          // console.log(response.data.data)
          const lists = response.data.data.subsidiaries
          const filteredList = lists.map(comp => ({
            value: comp.id,
            text: comp.subsidiary_name,
          }))
          this.companyList = this.initCompanyList.concat(filteredList)
        })
        .catch(() => {
          // console.log(error)
        })
    },
    checkRoleFilter() { // check filter options for each role
      // if (this.roles.isHOD) {
      //   console.log('masuk')
      //   this.listFilterBy = this.tempListFilter.filter(x => x !== 'Department')
      // } else {
      //   this.listFilterBy = this.tempListFilter
      // }
      this.listFilterBy = this.tempListFilter
    },
    async filter() {
      // console.log('masuk filter')
      this.currentPage = 1
      const data = new FormData()
      data.append('year', moment(this.selectedCycle).year())

      data.append('company', this.userDetails.business_unit)
      // data.append('department_id', this.userDetails.department_id)
      data.append('division_id', this.user.division)

      if (this.selectDepartment !== null){
        data.append('department_id', this.selectDepartment)
      }

      data.append('filter', 'department')

      data.append('status', 'active')
      // }

      // eslint-disable-next-line no-restricted-syntax
      // for (const pair of data.entries()) {
      //   console.log(`${pair[0]}, ${pair[1]}`)
      // }
      // data.forEach(x => {
      //   console.log(x)
      // })
      try {
        const response = await this.$axios.post(`${this.$baseUrl}/dashboard/graph`, data)
        this.graphValue = response.data.data
        this.catType = 'department'
        // this.show = false
        this.search = false
        this.refreshGraph = 0
        if (this.selectedCompany === 0){
          this.isOverall = true
        }

        this.changeToGraph('department', response.data.data)
      } catch (error) {
        console.error('ini err', error)
      }

      // const config = {
      //   method: 'post',
      //   url: `${this.$baseUrl}/dashboard/graph`,
      //   data,
      // }

      // this.$axios(config)
      //   .then(response => {
      //     console.log(response.data.data)
      //     console.log(this.filterBy)
      //     this.changeToGraph(this.filterBy === null ? '' : this.filterBy.toLowerCase(), response.data.data)
      //     this.graphValue = response.data.data
      //     this.catType = this.filterBy === null ? '' : this.filterBy.toLowerCase()
      //     setTimeout(() => {
      //       this.show = false
      //       this.refreshGraph = 0
      //     }, 800)
      //   })
      //   .catch(() => {
      //     this.show = false
      //     // console.log(error)
      //   })
    },
    changeToGraph(type, data) {
      // if (!this.roles.isAdmin) {
