            total4: item.summary_final_evaluation !== undefined ? item.summary_final_evaluation.perluperingatan : 0,
          }))
          // console.log('chart', this.filterByBar.chartOptions.xaxis)
      }
      // console.log('summary list after', this.summaryList)

      // eslint-disable-next-line no-plusplus
      this.apex1Counter++
      this.show = true
    },

    // setSummaryData(data, type){
