      <!-- {{graph}} -->
      <!-- {{categoryType}} -->

      <div :key="refreshGraph">
        <!-- {{ filterByBar.series }} -->
        <b-card
          height="800px"
          class=""
          cols="6"
        >
          <!-- {{ filterByBar.series }} -->
          <vue-apex-charts
            :key="apex1Counter"
            type="bar"
            height="auto"
            :options="filterByBar.chartOptions"
            :series="filterByBar.series"
            class=""
          />
        </b-card>

        <b-card
          height="800px"
          class=""
          cols="6"
        >
          <!-- {{ filterByLine.series }} -->
          <!-- {{ filterByLine.series.data }} -->
          <vue-apex-charts
            :key="apex1Counter"
            type="line"
            height="auto"
            :options="filterByLine.chartOptions"
            :series="filterByLine.series"
            class=""
          />
        </b-card>

      </div>
    </div>
  </div>
</template>

