<template>
  <div>
    <div v-if="categoryType === 'performance'">
      <b-card
        v-for="(item, idx) in graphData"
        :key="idx"
        class=""
        cols="6"
      >

        <p style="font-weight: 600; text-align: center; color: black">
          <!-- {{ filterByLine.series.data[idx].name }} -->
          {{ $t(chartName[idx]) }}
        </p>

        <!-- {{ idx }} -->
        <!-- {{ cat.summary }} -->
        <vue-apex-charts
          :key="apex1Counter"
          type="line"
          height="450px"
          :options="filterByLine.chartOptions"
          :series="filterByLine.series.data[idx]"
          class=""
        />
      </b-card>
    </div>
    <div v-else>
      <div
        v-if="graphData.length !== 0"
        class=""
      >
        <!-- {{graph}} -->
        <!-- {{categoryType}} -->
        <!-- {{ graphData }} -->

        <div

          :key="refreshGraph"
        >

          <div
            v-if="graphData.length > 6"
            class="row"
          >
            <b-card
              v-for="idx in range(currFirstIdx, currLastIdx)"
              :key="idx"
              height="800px"
              class="m-1"
              cols="6"
            >
              <p style="font-weight: 600; text-align: center; color: black">
                <!-- {{ filterByLine.chartOptions.title }} -->
                {{ $t(chartName[idx]) }}
              </p>
              <!-- {{ idx }} -->
              <!-- {{ cat.summary }} -->
              <vue-apex-charts
                :key="apex1Counter"
                type="line"
                :options="filterByLine.chartOptions"
                :series="filterByLine.series.data[idx]"
                class=""
                width="540px"
              />
            </b-card>
          </div>

          <div
            v-else
            class="row"
          >
            <b-card
              v-for="(item, idx) in graphData"
              :key="idx"
              height="800px"
              class="m-1"
              cols="6"
            >

              <p style="font-weight: 600; text-align: center; color: black">
                <!-- {{ filterByLine.series.data[idx].name }} -->
                {{ $t(chartName[idx]) }}
              </p>

              <!-- {{ idx }} -->
              <!-- {{ cat.summary }} -->
              <vue-apex-charts
                :key="apex1Counter"
                type="line"
                :options="filterByLine.chartOptions"
                :series="filterByLine.series.data[idx]"
                class=""
                width="540px"
              />
            </b-card>
          </div>

        </div>

        <b-pagination
          v-model="currentPage"
          class="d-flex justify-content-center"
          per-page="6"
          :total-rows="rows"
          @input="currFirstIdx = ((currentPage * 6) - 6), refreshGraph += 1, currLastIdx = ((currentPage * 6 > graphData.length) ? graphData.length : currentPage *6)"
        />
      </div>
    </div>

  </div>
</template>

